import React from 'react'

export default function KnowledgeListTile(props){
    return(
        <li>
            <div style={{display: 'flex', alignItems: 'center'}}>
                <span>
                    {props.name}
                </span>

                {
                    props.imageName &&
                    <img
                        src={`./dev-icons/${props.imageName}.png`}
                        style={{marginLeft: '10px'}}
                    />
                }
            </div>
        </li>
    )
}